.solution {
 display: flex;
 align-items: flex-start;
 justify-content: space-between;

 &__title {
  font-size: 44px;
  line-height: 50px;
  color: #232526;
 }

 &__main {
  max-width: 880px;
  width: 100%;

  textarea {
   font-size: 18px;
   line-height: 30px;

   &::placeholder {
    color: #a3a8bb;
   }

   &.ant-input {
    min-height: 300px;
    background: #fff;
    border: 1px solid #EEF2FF;
    box-sizing: border-box;
    border-radius: 10px;
    resize: none;
    padding: 27px 0 0 35px;
   }
  }

  &-upload {
   margin-bottom: 70px;

   .upload-list-inline {
    display: flex;
    flex-direction: column-reverse;

    .ant-upload {
     cursor: pointer;
     margin: 12px 0 0;
     color: #232526;

     .limitation-file {
      color: #a3a8bb;
      margin-left: 10px;
     }
    }
   }

   .ant-upload-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
   }

   .ant-upload-list-item {
    height: auto;
    border: 1px solid #2351ff;
    background: transparent;
    border-radius: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 0 8px 15px;
    margin-bottom: 10px;

   }

   .ant-upload-list-text .ant-upload-text-icon .anticon {
    display: none;
   }

   button:disabled {
      background: #a4B7fe;
   }

   .ant-upload-list-text .ant-upload-list-item-card-actions,
   .ant-upload-list-picture .ant-upload-list-item-card-actions {
    .ant-btn {
     height: 12px;
     background-color: transparent;
     background-repeat: no-repeat;
     background-position: center;
     background-image: url('../../../common/images/partners/close.svg');
     margin-left: 10px;
     margin-right: 8px;
    }
   }

   .ant-upload-list-item-error .ant-upload-list-item-card-actions .anticon {
    display: none;
   }
  }
 }
}
